import * as PIXI from "pixi.js";
import { lerp } from "../utils/tween";
import { mag, sub, add, mul, normalize } from "../utils/m2d";

const { max, min } = Math;

export class Pool {
    stage: PIXI.Container;
    bounds: number[][];
    poles: any;

    constructor(stage: PIXI.Container, bounds: number[][]) {
        this.stage = stage;
        this.bounds = bounds;
        this.poles = [];
    }

    addPole(p: number[]) {
        const pole = { p, sprite: PIXI.Sprite.from(PIXI.Texture.WHITE) };
        pole.sprite.anchor.set(0.5);
        pole.sprite.width = 16;
        pole.sprite.height = 16;
        pole.sprite.tint = 0x93b5c6;
        this.poles.push(pole);
        this.stage.addChild(pole.sprite);
    }

    getForce(p: number[]) {
        let F = [0, 0];
        const maxF = 8;
        const thd = 10;

        for (const pole of this.poles) {
            const dp = sub(p, pole.p);
            const str = 5 * max(0, lerp(maxF, 0, mag(dp) / thd));
            F = add(F, mul(normalize(dp), str));
        }

        F[0] +=
            max(0, lerp(maxF, 0, p[0] / thd)) +
            min(lerp(-maxF, 0, (this.bounds[1][0] - p[0]) / thd), 0);
        F[1] +=
            max(0, lerp(maxF, 0, p[1] / thd)) +
            min(lerp(-maxF, 0, (this.bounds[1][1] - p[1]) / thd), 0);

        return F;
    }

    render(W: number) {
        for (const pole of this.poles) {
            pole.sprite.position.set(
                (pole.p[0] * W) / 100,
                (pole.p[1] * W) / 100
            );
        }
    }
}
