import * as PIXI from "pixi.js";
import { Boid } from "./Boid";
import { Pool } from "./Pool";

const { floor, min, PI } = Math;

const canvas = document.querySelector("#main-canvas") as HTMLCanvasElement;
const W = min(window.innerWidth, 420);
const H = W * 1.7;
canvas.style.width = `${W}px`;
canvas.style.height = `${H}px`;

const app = new PIXI.Application({
    width: W,
    height: H,
    view: canvas,
    backgroundColor: 0x000000,
    resolution: window.devicePixelRatio || 1,
});

canvas.addEventListener("pointerdown", (e) => {
    const x = (e.clientX / W) * 100;
    const y = (e.clientY / W) * 100;
    pool.addPole([x, y]);
});

const pool = new Pool(app.stage, [
    [0, 0],
    [100, 170],
]);
const boids = Array.from({ length: 32 }).map(() => new Boid(app.stage, pool));

let t = 0;
app.ticker.add((dt) => {
    dt /= 60;
    t += dt;

    pool.render(W);
    boids.map((boid) => boid.update(dt, boids));
    boids.map((boid) => boid.render(W));
});
