export const identity = () => [1, 0, 0, 0, 1, 0, 0, 0, 1];

export const fromScaling = (sx, sy) => [sx, 0, 0, 0, sy, 0, 0, 0, 1];

export const fromRotation = (rad) => [
    Math.cos(rad),
    Math.sin(rad),
    0,
    -Math.sin(rad),
    Math.cos(rad),
    0,
    0,
    0,
    1,
];

export const fromTranslation = (tx, ty) => [1, 0, 0, 0, 1, 0, tx, ty, 1];

export const matmulv = (m, v) => [
    m[0] * v[0] + m[3] * v[1] + m[6] * v[2],
    m[1] * v[0] + m[4] * v[1] + m[7] * v[2],
    m[2] * v[0] + m[5] * v[1] + m[8] * v[2],
];

export const matmul = (...args) => {
    let m1;

    for (let m2 of args) {
        m1 = m1
            ? [
                  m1[0] * m2[0] + m1[3] * m2[1] + m1[6] * m2[2],
                  m1[1] * m2[0] + m1[4] * m2[1] + m1[7] * m2[2],
                  m1[2] * m2[0] + m1[5] * m2[1] + m1[8] * m2[2],

                  m1[0] * m2[3] + m1[3] * m2[4] + m1[6] * m2[5],
                  m1[1] * m2[3] + m1[4] * m2[4] + m1[7] * m2[5],
                  m1[2] * m2[3] + m1[5] * m2[4] + m1[8] * m2[5],

                  m1[0] * m2[6] + m1[3] * m2[7] + m1[6] * m2[8],
                  m1[1] * m2[6] + m1[4] * m2[7] + m1[7] * m2[8],
                  m1[2] * m2[6] + m1[5] * m2[7] + m1[8] * m2[8],
              ]
            : m2;
    }

    return m1;
};

export const add = (p, q) => [p[0] + q[0], p[1] + q[1]];
export const sub = (p, q) => [p[0] - q[0], p[1] - q[1]];
export const mul = (p, c) => [p[0] * c, p[1] * c];
export const div = (p, c) => [p[0] / c, p[1] / c];
export const dot = (p, q) => p[0] * q[0] + p[1] * q[1];
export const magSqr = (p) => dot(p, p);
export const mag = (p) => magSqr(p) ** 0.5;
export const distSqr = (p, q) => magSqr([p[0] - q[0], p[1] - q[1]]);
export const dist = (p, q) => distSqr(p, q) ** 0.5;
export const cross = (p, q) => p[0] * q[1] - q[0] * p[1];

export const normalize = (p) => {
    const m = mag(p);

    return [p[0] / m, p[1] / m];
};
