import * as PIXI from "pixi.js";
import { Pool } from "./Pool";
import { cross, distSqr, dot, mag, magSqr, sub } from "../utils/m2d";

const { abs, atan2, cos, sin, random, sign, PI } = Math;

const boidImg =
    "data:image/png;base64,";
const boidTex = PIXI.Texture.from(new URL(boidImg, import.meta.url).href);

export class Boid {
    sprite: PIXI.Sprite;
    stage: PIXI.Container;
    pool: Pool;
    p: number[];
    v: number;
    ang: number;

    constructor(stage: PIXI.Container, pool: Pool) {
        this.p = [random() * 100, random() * 170];
        this.v = 48;
        this.ang = 0;
        this.stage = stage;
        this.pool = pool;
        this.sprite = PIXI.Sprite.from(boidTex);
        this.sprite.anchor.set(0.5);
        this.sprite.width = 16;
        this.sprite.height = 16;
        this.sprite.tint = 0xec9942;
        this.stage.addChild(this.sprite);
    }

    update(dt: number, boids: Boid[]) {
        this.ang += random() * 0.04 - 0.02;
        const dir = [cos(this.ang), sin(this.ang)];

        for (const boid of boids) {
            if (boid === this) continue;

            const ds = distSqr(this.p, boid.p);

            if (ds > 400) continue;

            if (ds < 64) {
                const dp = sub(this.p, boid.p);
                const crdp = cross(dir, dp);
                this.ang += 0.02 * sign(crdp);
            } else {
                const bdir = [cos(boid.ang), sin(boid.ang)];
                const cr = cross(dir, bdir);
                this.ang += 0.01 * sign(cr) * (-dot(dir, bdir) + 1.3);
            }
        }

        const F = this.pool.getForce(this.p);

        if (magSqr(F) > 0 && dot(dir, F) < 0) {
            const cr = cross(dir, F);
            this.ang += 0.04 * mag(F) * sign(cr === 0 ? 1 : cr);
        }

        this.p[0] += dt * this.v * cos(this.ang);
        this.p[1] += dt * this.v * sin(this.ang);
    }

    render(W: number) {
        const { p, sprite } = this;

        sprite.rotation = this.ang;
        sprite.position.set((p[0] * W) / 100, (p[1] * W) / 100);
    }
}
