export function lerp(a, b, t) {
    return (1 - t) * a + t * b;
}

export function ilerp(a, b, v) {
    return (v - a) / (b - a);
}

export function remap(iMin, iMax, oMin, oMax, v) {
    return lerp(oMin, oMax, ilerp(iMin, iMax, v));
}

// Interpolates between 'b' and 'c', takes into account 'a' and 'd'.
export function cubic(a, b, c, d, t) {
    return (
        b +
        0.5 *
            t *
            (c -
                a +
                t * (2 * a - 5 * b + 4 * c - d + t * (3 * (b - c) + d - a)))
    );
}

export function smoothstart(t) {
    return t * t;
}

export function smoothstop(t) {
    const ti = 1 - t;
    return 1 - ti * ti;
}

// Google "smoothstep" or "Perlin fade function".
export function smoothstep1(t) {
    return t * t * (-2 * t + 3);
}

export function smoothstep2(t) {
    return t * t * t * (t * (t * 6 - 15) + 10);
}

// GLSL smoothstep function
export function smoothstep(edge0, edge1, x) {
    const t = Math.max(0, Math.min((x - edge0) / (edge1 - edge0), 1));
    return t * t * (3 - 2 * t);
}

// Interpolates between 'a' and 'd', with control points 'b' and 'c'.
export function bezier(a, b, c, d, t) {
    const ti = 1 - t;
    return (
        ti * ti * ti * a +
        3 * t * ti * ti * b +
        3 * t * t * ti * c +
        t * t * t * d
    );
}

export function sigm(x, m = 1) {
    return 1 / (1 + Math.E ** (-x * m));
}

export function tween({ from, to, duration, onUpdate, onComplete, easeFunc }) {
    let t = 0;
    easeFunc = easeFunc || ((x) => x);

    return (dt) => {
        if (t >= duration) {
            return;
        }

        t += dt;
        const progress = t / duration;
        if (progress >= 1) {
            onUpdate(to);
            onComplete();
        } else {
            if (typeof from === "number") {
                onUpdate(lerp(from, to, easeFunc(progress)));
            } else {
                const obj = {};
                Object.keys(from).forEach((k) => {
                    obj[k] = lerp(from[k], to[k], easeFunc(progress));
                });
                onUpdate(obj);
            }
        }
    };
}
